/* Global reset and body styling */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
}

h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
}

/* Container to center all content */
.container {
    width: 100%;
    max-width: 800px; /* Adjust the max width for large screens */
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Style for the list of options */
ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 20px;
}

li {
    margin: 10px 0;
}

button {
    margin: 10px 0;
    padding: 10px;
    font-size: 16px;
    width: 100%;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

p {
    font-size: 18px;
    text-align: center;
}

/* Media query for mobile responsiveness */
@media (max-width: 768px) {
    body {
        flex-direction: column;
    }

    .container {
        width: 100%;
        padding: 15px;
    }

    h1 {
        font-size: 1.5rem;
    }

    button {
        padding: 12px;
        font-size: 14px;
    }

    p {
        font-size: 16px;
    }
}